import { Component, OnInit } from '@angular/core';
import { FirestoreService } from 'src/app/services/firestore.service';
import * as moment from "moment";
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.css']
})
export class NotificationsComponent implements OnInit {
  notificationCount: number = 0;
	notifications: any;
  filterNotifications:any;
  limit:any = 5;
  skip:any=0;
  page = 0/ this.limit + 1;

  constructor(private fs: FirestoreService, private router: Router, private toastr: ToastrService) { }

  ngOnInit() {
    this.getAllnotifications();
  }
  getAllnotifications(){
    this.fs.getAllNotifications().subscribe(async (data: any) => {
      this.filterNotifications = data;
			this.notifications = data.slice(((0 + 1) - 1) * this.limit).slice(0, this.limit);
			await this.notifications.map((n) => (n.timestamp = moment(n.createdAt.seconds * 1000).fromNow()));
			this.notificationCount = data.length || 0;
      // console.log("not", this.notifications)
		});
  }
  markAllRead(){
    this.fs.updateReadStatusAll();
    this.toastr.info("Marked all as read!")
  }
  handleNotificationRedirect(reDirectRoute: string, notificationId: string) {
		this.fs.updateReadStatus(notificationId);
		this.router.navigateByUrl(reDirectRoute);
	}
  handlePagination(pageIndex) {
    // this.skip = (pageIndex - 1) * this.limit;
    const offset = (pageIndex - 1) * this.limit;
    this.notifications = this.filterNotifications.slice(offset).slice(0, this.limit);
	}
}
